import classNames from 'classnames'
import { observer } from 'mobx-react-lite'
import PropTypes from 'prop-types'
import React from 'react'
import styles from './Table.module.scss'

const Table = (props) => {
  return (
    <table className={styles.table}>
      <thead className={styles.head}>
        <tr className={classNames(styles.row, styles.headRow)}>
          {props.columns?.map((i, idx) => (
            <th
              key={idx}
              className={classNames(styles.cell, styles.headRowCell)}
            >
              {i.label}
            </th>
          ))}
        </tr>
      </thead>
      <tbody className={styles.body}>
        {props.data?.map((row, rowIdx) => (
          <tr
            className={classNames(styles.row, styles.bodyRow)}
            key={rowIdx}
          >
            {props.columns?.map(({ key }, colIdx) => {
              return (
                <td
                  className={classNames(
                    styles.cell,
                    styles.bodyRowCell
                  )}
                  key={colIdx}
                >
                  {row[key]}
                </td>
              )
            })}
          </tr>
        ))}
      </tbody>
    </table>
  )
}

Table.propTypes = {
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string,
      label: PropTypes.node,
    })
  ),
  data: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.node,
    })
  ),
}
Table.displayName = 'Table'
export default observer(Table)
